.container
{
    margin: 50px 0px;
}
.cards
{
    margin: 0 2% !important;
}
.infected
{
    border-bottom: 10px solid rgba(0,0,255,0.7);
}
.recovered
{
    border-bottom: 10px solid rgba(0,255,0,0.7);
}
.deaths
{
    border-bottom: 10px solid rgba(255,0,0,0.7);
}

@media (max-width:770px)
{
    .cards
    {
        margin: 2% 0 !important;
    }
}